import React, { Component  } from 'react'
import { StaticQuery, graphql } from "gatsby"


export default function Whatsapp () {
  
  return (
    <StaticQuery
      query = {graphql`
      query Whatsapp {
          allWpPage(filter: {slug: {eq: "whatsapp"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render = { data => (
        
       <div>Whatsapp</div>

      )}
    />
  )
}